<template>
  <div class="dialogue">
    <b-card no-body class="mb-1">
      <b-card-header header-tag="header" class="p-1" role="tab">
        <b-button
          block
          href="#"
          v-b-toggle="'accordion-' + lineID"
          variant="secondary"
          >{{ shortText }}</b-button
        >
      </b-card-header>
      <b-collapse
        :id="'accordion-' + lineID"
        accordion="line-accordion"
        role="tabpanel"
      >
        <b-card-body>
          <b-input-group class="mt-3">
            <b-input-group-text slot="prepend">Line Type</b-input-group-text>
            <b-form-select
              class="bg-light"
              slot="append"
              v-model="obj.type"
              :options="lineOptions"
              :plain="true"
            ></b-form-select>
          </b-input-group>
          <div v-if="obj.type === 'dialogue'">
            <b-input-group class="mt-3">
              <b-input-group-text slot="prepend">Speaker</b-input-group-text>
              <b-form-input id="speaker" v-model="obj.source" required>
              </b-form-input>
              <b-form-select
                class="bg-light"
                slot="append"
                v-model="obj.source_visible"
                :options="visibleOptions"
                :plain="true"
              ></b-form-select>
            </b-input-group>

            <b-input-group class="mt-3">
              <b-input-group-text slot="prepend">Expression</b-input-group-text>
              <b-form-input id="expression" v-model="obj.source_expression">
              </b-form-input>
            </b-input-group>

            <b-form-textarea
              class="mt-3"
              id="textarea"
              v-model="obj.text"
              placeholder="Text..."
              rows="6"
              max-rows="6"
            >
            </b-form-textarea>

            <br />
          </div>
          <div v-else-if="obj.type === 'narration'">
            <b-input-group class="mt-3">
              <b-input-group-text slot="prepend"
                >Clear Last Speaker</b-input-group-text
              >
              <b-form-select
                class="bg-light"
                slot="append"
                v-model="obj.source_visible"
                :options="yesnoOptions"
                :plain="true"
              ></b-form-select>
            </b-input-group>
            <b-form-textarea
              class="mt-3"
              id="textarea"
              v-model="obj.text"
              placeholder="Text..."
              rows="6"
              max-rows="6"
            >
            </b-form-textarea>

            <br />
          </div>
          <div v-else-if="obj.type === 'destination'">
            <b-input-group class="mt-3">
              <b-input-group-text slot="prepend"
                >Destination</b-input-group-text
              >
              <b-form-select
                class="bg-light"
                slot="append"
                v-model="obj.destination"
                :options="sceneOptions"
                :plain="true"
              ></b-form-select>
            </b-input-group>

            <br />
          </div>
          <div v-else-if="obj.type === 'choice'">
            <div class="choice" v-for="(choice, key) in obj.choices" :key="key">
              <b-input-group class="mt-3">
                <b-input-group-text slot="prepend">Choice</b-input-group-text>
                <b-form-input v-model="choice.text" required> </b-form-input>
              </b-input-group>
              <b-input-group class="mt-3">
                <b-input-group-text slot="prepend"
                  >Destination</b-input-group-text
                >
                <b-form-select
                  slot="append"
                  class="bg-light"
                  v-model="choice.destination"
                  :options="sceneBlankOptions"
                  :plain="true"
                ></b-form-select>
              </b-input-group>
            </div>
            <b-button
              block
              href="#"
              class="mt-3"
              variant="info"
              v-on:click="addChoice"
              >Add Choice</b-button
            >
          </div>
          <b-button
            block
            href="#"
            class="mt-3"
            variant="danger"
            v-on:click="deleteLine"
            >Delete Line</b-button
          >
        </b-card-body>
      </b-collapse>
    </b-card>
  </div>
</template>

<script>
export default {
  name: "Line",
  props: {
    obj: Object,
    sceneIDs: Array,
    currentSceneID: String,
    index: Number
  },
  methods: {
    deleteLine: function() {
      this.$emit("deleteLine");
    },
    addChoice: function() {
      this.obj.choices.push({
        choice: null,
        destination: ""
      });
    }
  },
  computed: {
    shortText: function() {
      if (this.obj.destination)
        return `${this.obj.type} : ${this.obj.destination}`;
      if (this.obj.choices) return `choices`;
      if (!this.obj.text) return `new Line`;
      let short = this.obj.text.substr(0, 30);
      if (short.length === 30) return `${this.obj.type} : ${short}...`;
      else return `${this.obj.type} : ${short}`;
    },
    lineID: function() {
      return this.currentSceneID + "-" + this.index;
    },
    visibleOptions: function() {
      return [
        {
          text: "Visible",
          value: true
        },
        {
          text: "Not Visible",
          value: false
        }
      ];
    },
    yesnoOptions: function() {
      return [
        {
          text: "Yes",
          value: true
        },
        {
          text: "No",
          value: false
        }
      ];
    },
    lineOptions: function() {
      return [
        {
          text: "dialogue",
          value: "dialogue"
        },
        {
          text: "Narration",
          value: "narration"
        },
        {
          text: "Destination",
          value: "destination"
        },
        {
          text: "Choice",
          value: "choice"
        }
      ];
    },
    sceneOptions: function() {
      return this.sceneIDs
        .filter(scene => scene !== this.currentSceneID)
        .map(scene => {
          return {
            text: scene,
            value: scene
          };
        });
    },
    sceneBlankOptions: function() {
      return ["", ...this.sceneIDs]
        .filter(scene => scene !== this.currentSceneID)
        .map(scene => {
          return {
            text: scene,
            value: scene
          };
        });
    }
  }
};
</script>

<style scoped>
.collapsible {
  background-color: #777;
  color: white;
  cursor: pointer;
  padding: 18px;
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  font-size: 15px;
  text-transform: capitalize;
}
.collapsible:hover {
  background-color: #555;
}

.active {
  background-color: #555;
  max-height: 100%;
}

.inactive {
  max-height: 0;
}

.content {
  padding: 0 18px;
  overflow: hidden;
  transition: max-height 0.2s ease-out;
  background-color: #f1f1f1;
}
</style>
