<template>
  <div id="app">
    <b-container fluid>
      <b-row>
        <b-col>
          <b-row>
            <b-col>
              <b-form-file
                v-model="file"
                :state="Boolean(file)"
                placeholder="Choose a file..."
                drop-placeholder="Drop file here..."
              ></b-form-file>
            </b-col>
            <b-col>
              <b-button block href="#" variant="success" v-on:click="saveFile"
                >Save</b-button
              >
            </b-col>
          </b-row>
          <draggable
            v-model="scenes"
            group="scenes"
            ghost-class="ghost"
            @start="drag = true"
            @end="drag = false"
          >
            <GameScene
              v-for="(scene, key) in scenes"
              :key="key"
              :scene="scene"
              :sceneIDs="sceneIDs"
              @deleteScene="deleteScene(key)"
            >
            </GameScene>
          </draggable>
          <b-button block href="#" variant="info" v-on:click="addScene"
            >Add Scene</b-button
          >
        </b-col>
        <b-col>
          <b-row>
            <b-col>
              <b-form-file
                v-model="resource_file"
                :state="Boolean(resource_file)"
                placeholder="Choose a file..."
                drop-placeholder="Drop file here..."
              ></b-form-file>
            </b-col>
            <b-col>
              <b-button
                block
                href="#"
                variant="success"
                v-on:click="saveResourceFile"
                >Save</b-button
              >
            </b-col>
          </b-row>
          <b-row>
            <b-card
              class="mt-3"
              v-for="(resource, key) in resources"
              :key="key"
            >
              <b-col>
                <b-input-group class="mt-3">
                  <b-input-group-text slot="prepend">Path</b-input-group-text>
                  <b-input-group-text slot="prepend"
                    >/www/assets/</b-input-group-text
                  >
                  <b-form-select
                    class="bg-light"
                    slot="prepend"
                    v-model="resource.folder"
                    :options="assetOptions"
                    :plain="true"
                  ></b-form-select>
                  <b-form-input v-model="resource.url"></b-form-input>
                </b-input-group>
              </b-col>
              <b-col>
                <b-input-group class="mt-3">
                  <b-input-group-text slot="prepend">ID</b-input-group-text>
                  <b-form-input
                    v-model="resource.key"
                    placeholder="If left blank, the file name will default"
                  ></b-form-input>
                </b-input-group>
              </b-col>
            </b-card>
          </b-row>
          <b-button
            block
            class="mt-3"
            href="#"
            variant="info"
            v-on:click="addResource"
            >Add Resource</b-button
          >
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import GameScene from "./components/GameScene.vue";
import draggable from "vuedraggable";

export default {
  name: "app",
  components: {
    draggable,
    GameScene
  },
  data: function() {
    return {
      file: null,
      resource_file: null,
      resources: [
        {
          folder: "images",
          url: null,
          key: null
        }
      ],
      scenes: [
        {
          id: "root",
          lines: [],
          setting: null
        }
      ]
    };
  },
  watch: {
    file: function() {
      this.processFile(this.file);
    },
    resource_file: function() {
      this.processResourceFile(this.resource_file);
    }
  },
  methods: {
    addScene: function() {
      this.scenes.push({
        id: null,
        lines: [],
        setting: null
      });
    },
    addResource: function() {
      this.resources.push({
        folder: "images",
        url: null,
        key: null
      });
    },
    processFile: function(data) {
      let reader = new FileReader();
      reader.onload = (() => {
        return e => {
          this.scenes = JSON.parse(e.target.result);
        };
      })(data);
      reader.readAsText(data);
    },
    processResourceFile: function(data) {
      let reader = new FileReader();
      reader.onload = (() => {
        return e => {
          this.resources = JSON.parse(e.target.result).dataKey.files;
          this.resources.forEach(resource => {
            let path = resource.url.split("/");
            resource.folder = path.shift();
            resource.url = path.join("/");
          });
        };
      })(data);
      reader.readAsText(data);
    },
    saveFile: function() {
      let filename = this.file.name || "new_file.json";
      window.downloadFile(
        filename,
        JSON.stringify(this.scenes, null, 2),
        "text/json"
      );
    },
    saveResourceFile: function() {
      let filename = this.resource_file.name || "new_resource.json";
      let save_resources = JSON.parse(JSON.stringify(this.resources));
      save_resources.forEach(resource => {
        if (!resource.key) {
          if (resource.url) {
            let path = resource.url.split("/");
            resource.key = path[path.length - 1].split(".")[0].toLowerCase();
          }
        }
        resource.url = resource.folder + "/" + resource.url;
        if (resource.url.includes(".json")) resource.type = "json";
        else if (resource.url.includes(".png")) resource.type = "image";
        delete resource.folder;
      });
      window.downloadFile(
        filename,
        JSON.stringify(
          {
            dataKey: {
              path: "/www/assets/",
              files: save_resources
            }
          },
          null,
          2
        ),
        "text/json"
      );
    },
    deleteScene: function(index) {
      this.scenes.splice(index, 1);
    }
  },
  computed: {
    sceneIDs: function() {
      return this.scenes.map(scene => scene.id);
    },
    assetOptions: function() {
      return [
        {
          text: "images/",
          value: "images"
        },
        {
          text: "json/",
          value: "json"
        },
        {
          text: "script/",
          value: "script"
        }
      ];
    }
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.ghost {
  opacity: 0.5;
  background: #c8ebfb;
}
</style>
