<template>
  <div class="scene">
    <b-card no-body class="mb-1">
      <b-card-header header-tag="header" class="p-1" role="tab">
        <b-button
          block
          href="#"
          v-b-toggle="'accordion-' + scene.id"
          variant="secondary"
          >{{ scene.id }}</b-button
        >
      </b-card-header>
      <b-collapse
        :id="'accordion-' + scene.id"
        accordion="scene-accordion"
        role="tabpanel"
      >
        <b-card-body>
          <b-input-group class="mt-3">
            <b-input-group-text slot="prepend">Scene ID</b-input-group-text>
            <b-form-input id="speaker" v-model="scene.id" required>
            </b-form-input>
          </b-input-group>
          <b-input-group class="mt-3">
            <b-input-group-text slot="prepend">Setting</b-input-group-text>
            <b-form-input id="speaker" v-model="scene.setting" required>
            </b-form-input>
          </b-input-group>
          <br />
          <draggable
            v-model="scene.lines"
            group="scenes"
            ghost-class="ghost"
            @start="drag = true"
            @end="drag = false"
          >
            <GameLine
              v-for="(game_line, key) in scene.lines"
              :key="key"
              :obj="game_line"
              :sceneIDs="sceneIDs"
              :currentSceneID="scene.id"
              :index="key"
              @deleteLine="deleteLine(key)"
            >
            </GameLine>
          </draggable>
          <b-row>
            <b-col>
              <b-button block href="#" variant="info" v-on:click="addLine"
                >Add Line</b-button
              >
            </b-col>
            <b-col>
              <b-button block href="#" variant="danger" v-on:click="deleteScene"
                >Delete Scene</b-button
              >
            </b-col>
          </b-row>
        </b-card-body>
      </b-collapse>
    </b-card>
  </div>
</template>

<script>
import GameLine from "./Line.vue";
import draggable from "vuedraggable";

export default {
  name: "GameScene",
  components: {
    draggable,
    GameLine
  },
  props: {
    scene: Object,
    sceneIDs: Array
  },
  methods: {
    addLine() {
      this.scene.lines.push({
        type: "dialogue",
        source: null,
        source_visible: false,
        source_expression: null,
        text: null
      });
    },
    deleteLine(index) {
      this.scene.lines.splice(index, 1);
    },
    deleteScene() {
      this.$emit("deleteScene");
    }
  }
};
</script>

<style scoped>
.collapsible {
  background-color: #777;
  color: white;
  cursor: pointer;
  padding: 18px;
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  font-size: 15px;
}
.collapsible:hover {
  background-color: #555;
}

.active {
  background-color: #555;
  max-height: 100%;
}

.inactive {
  max-height: 0;
}

.content {
  padding: 0 18px;
  overflow: hidden;
  transition: max-height 0.2s ease-out;
  background-color: #f1f1f1;
}
</style>
